import { StoryModel, Chapter, HomeStoryStore } from "./StoryStore";

it("Story Modal", () => {
    const item = Chapter.create({
        title: "Chapter1",
        content: "nothing to be written here"
    })

    expect(item.content).toBe("nothing to be written here")
})


